import React, { useEffect, Fragment, useState } from "react";
import {
  Field,
  useField,
  FieldAttributes,
  FastField,
  FieldProps,
} from "formik";
import { ElementInputProps, LabelValue } from "../../common/ElementInputProps";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { TextField, CircularProgress } from "@material-ui/core";
import "./autocomplete.css";
import { getHttp } from "../../common/HttpService";
import { useFormContext, Controller } from "react-hook-form";

type MySelectFieldProps = {
  options?: LabelValue[];
  remoteUrl?: string;
  reload?: number;
  kvMapping?: any;
  onSelect?: any;
} & ElementInputProps &
  FieldAttributes<{}>;

// { options, required = false, name,
//     label, helperText, type = "text", remoteUrl, kvMapping }

const ApplnAutoCompleteAsync: React.FC<MySelectFieldProps> = (props) => {
  const [opts, setOpts] = useState({ q: "", timerId: null, dataset: [] });
  const [loading, setLoading] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const renders = React.useRef(0);
  const methods = useFormContext();
  const { errors, getValues, setValue, register, control } = methods;
  let timerId: any = null;

  const autocompleteSearch = (value: any) => {
    setLoading(true);
    let qurl = props.remoteUrl + "&q=" + value;
    getHttp({ url: qurl }, false)
      .then((e) => {
        let dItems: any = [];
        console.log(e);
        dItems = e.map((ele: any) => {
          let labEle = "ele." + props.kvMapping.label;
          let labelv = eval(labEle);
          let valEle = "ele." + props.kvMapping.value;
          let val = eval(valEle);
          return { label: labelv, value: val };
        });
        console.log(dItems);
        setOpts((prevState) => ({
          ...prevState,
          dataset: dItems,
        }));
        setLoading(false);
      })
      .catch((error) => {
        setLoading(false);
        let eitems = [
          { label: "Unable to Fetch the data: " + error, value: "-3333" },
        ];
        setOpts((prevState) => ({
          ...prevState,
          dataset: eitems,
        }));
      });
  };

  const handleChange = (event: any, option: LabelValue) => {
    setRefresh(true);
    if (option != null) {
      setValue(props.name, option.value);
      methods.trigger(option.value);
      props.onSelect(option.value);
    } else {
      event.target.value = "";
      setValue(props.name, "");
      //props.onSelect( "",);
    }
  };

  const onKeyPressInput = (event: any, value: string, reason: string) => {
    if (event.type === "click") {
      return;
    }
    setRefresh(true);

    if (props.remoteUrl && value !== "") {
      clearTimeout(opts.timerId);

      timerId = setTimeout(() => {
        autocompleteSearch(value);
      }, 200);
      setOpts((prevState) => ({
        ...prevState,
        q: value,
        timerId: timerId,
      }));
    }
  };

  const handleClose = (event: any, reason: any) => {
    if (props.remoteUrl) {
      setOpts((prevState) => ({
        ...prevState,
        dataset: [],
      }));
    }
    console.log("closing");
    setRefresh(false);
  };

  const intializeList = () => {
    if (props.options?.length > 0) {
      let ds: any = props.options;
      if (props.kvMapping) {
        ds = props.options.map((opt) => {
          let labEle = "opt." + props.kvMapping.label;
          let labelv = eval(labEle);
          let valEle = "opt." + props.kvMapping.value;
          let val = eval(valEle);
          return { label: labelv, value: val };
        });
      }
      setOpts({
        q: "",
        dataset: ds,
        timerId: null,
      });

      console.log(ds);
    }
  };

  useEffect(() => {}, []);

  return (
    <>
      <Controller
        control={control}
        name={props.name}
        render={(inpProps) => (
          <>
            <Autocomplete
              freeSolo
              autoHighlight
              autoComplete={false}
              options={opts.dataset}
              getOptionLabel={(option: LabelValue) => option.label}
              getOptionSelected={(option: LabelValue, value: any) => {
                return true;
              }}
              fullWidth
              onChange={handleChange}
              onInputChange={onKeyPressInput}
              onClose={handleClose}
              renderInput={(params: any) => {
                return (
                  <>
                    <div className="autoSelect-txt-div">
                      <TextField
                        autoComplete="false"
                        variant="outlined"
                        size="small"
                      
                        {...params}
                        label={props.label}
                      />
                      {loading && (
                        <CircularProgress className="auto-search-loading-icon" />
                      )}
                    </div>
                  </>
                );
              }}
            />
          </>
        )}
      />
    </>
  );
};

export default ApplnAutoCompleteAsync;
