import React, { useState } from 'react';
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';
import Geocode from "react-geocode";
// import mapStyles from './mapStyle';
import config from './MapConfig';

function MapComponent(props) {
  const [zoomLevel, setZoomLevel] = useState(config.zoomLevel)
  const [lat, setLat] = useState(config.lat || 25.2048);
  const [lng, setLng] = useState(config.lng || 55.2708);
 
  const handleMapClick = (ref, map, ev) => {
    const location = ev.latLng;
    setLat(location.lat());
    setLng(location.lng());
    Geocode.fromLatLng(location.lat(),location.lng(),config.apiKey).then(
      response => {
        const address = response.results[0].formatted_address;
        console.log("formatted_address :: " + address);
        props.handleClick(address);
      },
      error => {
        console.error(error);
      }
    );
    map.setZoom(15);
    map.panTo(location);
  };

  return (
    <div style={{width:"50%%", height: "50vh"}}>
      <Map
        google={props.google}
        zoom={zoomLevel}
        initialCenter={{
          lat,
          lng
        }}
        streetViewControl={config.streetViewControl}
        gestureHandling={config.gestureHandling}
        onClick={handleMapClick}
        mapTypeControl={config.mapTypeControl}
        fullscreenControl={config.fullscreenControl}
      >
        <Marker
          position={{ lat: lat, lng: lng }}
          
         /> 
      </Map>
    </div>
  );
};

export default GoogleApiWrapper({ apiKey: config.apiKey })(MapComponent);